@use "../abstracts/variables";
@use "../abstracts/mixins";

%margin-title {
  margin-bottom: 25px;
}

body {
  color: variables.$color-text;
  font-family: variables.$font-family--general;
  font-size: variables.$font-size--general-m;
  font-weight: 300;
}

main[role="main"] {
  display: flex;
  flex-direction: column;
}

.margin-title {
  @extend %margin-title;
}

.hidden {
  display: none !important;
}

.title {
  display: block;
  color: variables.$color-title;
  font-family: variables.$font-family--title;
  font-size: variables.$font-size--mobile-h1;
  font-weight: 900;
  line-height: variables.$line-height--mobile-h1;
  @extend %margin-title;

  @include mixins.media('tablet-l') {
    font-size: variables.$font-size--desk-h1;
    line-height: variables.$line-height--desk-h1;
  }
}

h1,
.h1 {
  color: variables.$color-title;
