@use '../abstracts/variables';

.breadcrumbs {
  margin-bottom: calc(variables.$margin-section / 2);

  &-section {
    display: flex;
    order: 1;
    width: 100%;
  }

  &__list {
    list-style-type: none;
    margin: 0;
    padding: 0;
    font-size: 12px;
    line-height: 13.38px;
  }

  &__item, &__link {
    color: variables.$color-principal;
  }

  &__item {
    display: inline-block;

    &:not(:last-child):after {
      content: '/';
      position: relative;
      display: inline-block;
      width: .20em;
      height: .20em;
      margin: 0 4px;
      color: variables.$color-principal;
    }
  }

  &__link {
    text-decoration: none;

    &[aria-current="page"] {
      color: inherit;
      font-weight: 400;
      text-decoration: none;
    }
  }
}
