@use "../abstracts/variables";
@use "../abstracts/mixins";

.blog, .breadcrumbs-section {
  max-width: 1180px;
}

.blog {
  width: 100%;

  &__header {
    margin: 40px auto 20px;
    display: flex;
    flex-direction: column;
    text-align: center;

    @include mixins.media('tablet') {
      margin: variables.$margin-section auto 22px;
    }

    &-title {
      text-transform: uppercase;
      margin-bottom: 40px;
      font-size: variables.$font-size--mobile--h1-customer;
      line-height: variables.$line-height--mobile--h1-customer;
      color: variables.$color-black;

      @include mixins.media('tablet') {
        margin-bottom: 34px;
        font-size: variables.$font-size--mobile-h1;
        line-height: variables.$line-height--mobile-h1;
      }
    }
  }

  &__tags {
    overflow-x: auto;

    a, a:visited {
      color: variables.$color-principal;
    }

    a {
      font-weight: variables.$font-regular;
      opacity: .5;
      text-decoration: underline;
      text-transform: uppercase;
      font-size: 13px;
      line-height: 20px;
    }

    .active-tag a {
      font-weight: variables.$font-semibold;
      opacity: 1;
    }

    &-elements {
      flex-wrap: wrap;
      list-style: none;
      @include mixins.boxFlex(space-evenly);

      @include mixins.mediaCustom('min-width: 576px') {
        @include mixins.boxFlex();
      }

      &-tag {
        display: inline-block;
        margin-right: 0 !important;
        padding: 0 18px 12px;
        white-space: nowrap;

        @include mixins.mediaCustom('min-width: 576px') {
          margin-right: 36px !important;
          padding: 0 0 12px;
        }
        @include mixins.media('tablet') {
          margin-right: 60px !important;
        }
      }

      &-tag:last-of-type {
        margin: 0 !important;
      }
    }
  }

  &__articles {
    ul {
      list-style: none;
    }

    &--content {
      margin-bottom: 32px;
      --column-gap: 20px;
      row-gap: 24px;

      @include mixins.mediaCustom('max-width: 749px') {
        grid-template-columns: var(--columns , repeat(2, 1fr));
      }
      @include mixins.mediaCustom('max-width: 576px') {
        grid-template-columns: var(--columns , 1fr);
      }

      @include mixins.media('desktop') {
        margin-bottom: variables.$margin-section;
        --column-gap: 25px;
      }
    }

    .blog-card {
      position: relative;
      color: variables.$color-black;

      .blog-card__image img {
        width: 100%;
        height: 100%;
        aspect-ratio: 21 / 9;
        object-fit: cover;
      }

      &__wrapper-content--excerpt {
        margin-bottom: 60px;
        padding: 16px 0 0;
        font-size: variables.$font-size--general-l;
        line-height: variables.$line-height--general-l;
        text-align: center;
        font-weight: variables.$font-regular;
        font-family: variables.$font-family--title;
        @include mixins.excerptForLine(5);
      }

      .blog-card__link {
        position: absolute;
        bottom: 0;
        left: 50%;
        min-width: 124px;
        transform: translateX(-50%);
      }

      &--no-excerpt {
        display: block;
        margin-bottom: 60px;
      }
    }

    .pagination {
      margin: 0 0 32px;

      @include mixins.media('desktop') {
        margin: 60px 0;
      }
    }

    .pagination--nav ul {
      align-items: flex-end;
    }

    .pagination--nav ul li {
      margin: 0 31px 0 0;
      font-family: variables.$font-family--title;
      font-size: variables.$font-size--general-l;

      a, a:visited {
        color: variables.$color-background-2;
      }
    }

    .pagination--nav ul li:last-of-type {
      margin: 0;
    }

    .pagination--nav ul li span {
      text-decoration: underline;
      font-size: variables.$font-size--general-up-l;
      color: variables.$color-principal;
    }

    .pagination .caret {
      transform: translateY(4px);
    }

    .pagination .caret svg {
      width: 8px;
    }
  }
}

.breadcrumbs {
  margin-bottom: 40px !important;

  @include mixins.media('tablet') {
    margin-bottom: variables.$margin-section !important;
  }
}
