@use '../abstracts/variables';
@use '../abstracts/mixins';
@use '../theme/theme';

.article {
  overflow: hidden;

  .slider-section .slider-content  {
    button.swiper-button-prev {
      left: -40px;
    }

    button.swiper-button-next {
      right: -40px;
    }
  }
}

.article__hero {
  position: relative;
  padding-top: 16px;

  @include mixins.media('tablet-l') {
    padding-top: 70px;
  }

  .letter-icon {
    bottom: -848px;
    left: initial;
    right: -8px;
    top: initial;
  }
}

.article__hero--image {
  position: relative;
  margin-bottom: 24px;
  padding-bottom: 56.25%;

  @include mixins.media('tablet-l') {
    margin-bottom: 48px;
    padding-bottom: 0;
  }

  img {
    position: absolute;
    top: 0;
    height: 100%;
    width: 100%;
    object-fit: cover;

    @include mixins.media('tablet-l') {
      position: initial;
      height: 208px;
    }
  }
}

.article__hero--date {
  margin-bottom: 24px;
  line-height: variables.$line-height--general-l;
  text-align: center;

  @include mixins.media('tablet-l') {
    margin-bottom: 48px;
  }
}

.article__hero--name {
  margin-bottom: 8px;

  h1 {
    margin: 0;
    font-size: variables.$font-size--mobile-h2;
    line-height: variables.$line-height--mobile-h2;
    text-align: center;

    @include mixins.media('tablet-l') {
      font-size: variables.$font-size--desk-h2;
      line-height: variables.$line-height--desk-h2;
    }
  }
}

.article__hero--tags {
  margin-bottom: 8px;
  font-family: variables.$font-family--title;
  @include mixins.boxFlex;

  &-tag {
    padding: 5px 28px 0 28px;
    font-family: inherit;
    line-height: variables.$line-height--general-m;

    @include mixins.media('desktop') {
      font-size: variables.$font-size--general-l;
      line-height: variables.$line-height--general-l;
    }
  }
}

.article__content {
  text-align: left;
  line-height: variables.$line-height--general-m;

  @include mixins.media('desktop') {
    font-size: variables.$font-size--general-l;
    line-height: variables.$line-height--general-l;
  }

  img {
    object-fit: cover;
    margin: 10px 0;
  }

  > * {
    text-align: justify;
  }

  h2 {
    @extend .h2;
  }

  h3 {
    @extend .h3;
  }

  h4 {
    @extend .h4;
  }

  p {
    font-size: inherit;
    line-height: inherit;
  }

  strong {
    font-size: inherit;
    line-height: inherit;
    font-weight: 700;
  }

  h2,
  h3,
  h4,
  h5,
  h6 {
    *,
    & {
      font-weight: 400;
    }
  }

  a {
    font-size: inherit;
    line-height: inherit;
  }

  ul,
  ol {
    font-size: inherit;
    line-height: inherit;
    list-style-position: inside;
  }

  .slider-section  {
    h2 {
      font-weight: 900;
    }
  }

  .swiper-button-disabled {
    opacity: .5;
    cursor: initial;
  }
}

.article__button {
  align-items: center;
  align-content: center;
}

.btn-article {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  left: 1%;
}

.social-icons {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: start;
  left: 1%;

  &__title {
    label {
      font-size: 18px;
      font-weight: 700;
      line-height: 30px;
    }
  }

  &__list {
    list-style: none;
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
  }
}

.social-icons li a svg {
  width: 25px;
  height: auto;
  margin: 5px;
}

.article__video {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
  max-width: 100%;
  margin: 10px 0;
}

.article__video iframe,
.article__video object,
.article__video embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
