@use "../abstracts/variables";
@use "../abstracts/mixins";

.article-banner {
  position: relative;
  margin-bottom: 24px;
  margin-top: 24px;
  background-color: variables.$color-secondary;
  padding: 24px 16px;
  @include mixins.boxFlex(initial, center, column);

  @include mixins.media("desktop") {
    padding: 48px;
    flex-direction: row;
  }

  @include mixins.mediaCustom("max-width: 991px") {
    margin-left: calc(0rem - variables.$paddings-side--movil);
    margin-right: calc(0rem - variables.$paddings-side--movil);
  }

  &.img-position--D {
    @include mixins.mediaCustom("max-width: 1199px") {
      flex-direction: column-reverse;
    }

    .article-banner__img-container {
      @include mixins.mediaCustom("max-width: 1199px") {
        margin-bottom: 0;
        margin-top: 24px;
      }
    }
  }

  &.img-position--R {
    @include mixins.media("desktop") {
      flex-direction: row-reverse;
    }

    .article-banner__img-container {
      @include mixins.media("desktop") {
        margin-right: 0;
        margin-left: 24px;
      }
    }
  }

  .letter-icon {
    display: none;

    @include mixins.media("desktop") {
      display: block;
      transform: translateX(-13vw);
    }

    @include mixins.media("desktop-m") {
      transform: translateX(-14vw);
    }

    @include mixins.mediaCustom("min-width: 1520px") {
      transform: translateX(-15vw);
    }

    @include mixins.mediaCustom("min-width: 1620px") {
      transform: translateX(-17vw);
    }

    @include mixins.mediaCustom("min-width: 1720px") {
      transform: translateX(-19vw);
    }

    @include mixins.mediaCustom("min-width: 1820px") {
      transform: translateX(-20vw);
    }

    @include mixins.mediaCustom("min-width: 1920px") {
      transform: translateX(-21vw);
    }

    &.letter-icon--top-left {
      top: -100px;
    }
  }
}

.article-banner__img-container {
  position: relative;
  display: block;
  width: 100%;
  margin-bottom: 24px;
  padding-bottom: 56.25%;
  line-height: 0;

  @include mixins.media("desktop") {
    padding-bottom: 0;
    margin-bottom: 0;
    width: auto;
  }

  img {
    bottom: 0;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    height: 100%;
    width: 100%;
    object-fit: cover;

    @include mixins.media("desktop") {
      position: initial;
      height: 182px;
      margin-bottom: 0;
      margin-right: 24px;
      max-width: initial;
      width: 325px;
    }
  }
}

.article-banner__description {
  text-align: left;
  line-height: variables.$line-height--general-m;
  width: 100%;

  @include mixins.media("desktop") {
    font-size: variables.$font-size--general-l;
    line-height: variables.$line-height--general-l;
  }
}
